<template>
  <div class="container d-flex justify-content-center flex-column"
       style="height: 85vh; overflow-y: scroll">
    <div id="skill-chart">
      <div id="myCanvasContainer" class="text-center">
        <canvas id="myCanvas" height="400px" width="400px">
          <p>Anything in here will be replaced on browsers that support the canvas element</p>
        </canvas>
      </div>
      <div id="tags">
        <ul>
          <li><a>PHP</a></li>
          <li><a>Codeigniter</a></li>
          <li><a>Javascript</a></li>
          <li><a>Vue.js</a></li>
          <li><a>HTML</a></li>
          <li><a>CSS</a></li>
          <li><a>Node.js</a></li>
          <li><a>Gulp</a></li>
          <li><a>Webpack</a></li>
          <li><a>Bootstrap</a></li>
          <li><a>jQuery</a></li>
          <li><a>Chart.js</a></li>
          <li><a>MySQL</a></li>
        </ul>
      </div>
    </div>
    <div class="w-100">
      <div class="row">
        <div class="col-md-6">
          <h5 class="text-center mt-2">Front End</h5>
          <p>HTML</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 95%" aria-valuenow="95" aria-valuemin="0"
                 aria-valuemax="100">95%</div>
          </div>
          <p>CSS</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 96%" aria-valuenow="95" aria-valuemin="0"
                 aria-valuemax="100">95%</div>
          </div>
          <p>Javascript</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 90%" aria-valuenow="90" aria-valuemin="0"
                 aria-valuemax="100">90%</div>
          </div>
          <p>Bootstrap</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 90%" aria-valuenow="90" aria-valuemin="0"
                 aria-valuemax="100">90%</div>
          </div>
          <p>jQuery</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 95%" aria-valuenow="95" aria-valuemin="0"
                 aria-valuemax="100">95%</div>
          </div>
          <p>Gulp</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 79%" aria-valuenow="79" aria-valuemin="0"
                 aria-valuemax="100">79%</div>
          </div>
          <p>Webpack</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 89%" aria-valuenow="89" aria-valuemin="0"
                 aria-valuemax="100">89%</div>
          </div>
          <p>Vue.js</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 90%" aria-valuenow="90" aria-valuemin="0"
                 aria-valuemax="100">90%</div>
          </div>
          <p>Node.js</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 75%" aria-valuenow="75" aria-valuemin="0"
                 aria-valuemax="100">75%</div>
          </div>
          <p>Chart.js</p>
          <div class="progress bg-light">
            <div class="progress-bar" role="progressbar" style="width: 90%" aria-valuenow="90" aria-valuemin="0"
                 aria-valuemax="100">90%</div>
          </div>
        </div>
        <div class="col-md-6">
          <h5 class="text-center mt-2">Back End</h5>
          <p>PHP</p>
          <div class="progress bg-light">
            <div class="progress-bar bg-success" role="progressbar" style="width: 95%" aria-valuenow="95" aria-valuemin="0"
                 aria-valuemax="100">95%</div>
          </div>
          <p>MySQL</p>
          <div class="progress bg-light bg-light">
            <div class="progress-bar bg-success" role="progressbar" style="width: 97%" aria-valuenow="97%" aria-valuemin="0"
                 aria-valuemax="100">97%</div>
          </div>
          <p>MongoDb</p>
          <div class="progress bg-light">
            <div class="progress-bar bg-success" role="progressbar" style="width: 88%" aria-valuenow="88" aria-valuemin="0"
                 aria-valuemax="100">88%</div>
          </div>
          <p>CodeIgniter</p>
          <div class="progress bg-light">
            <div class="progress-bar bg-success" role="progressbar" style="width: 96%" aria-valuenow="96" aria-valuemin="0"
                 aria-valuemax="100">96%</div>
          </div>
          <p>Node REST API</p>
          <div class="progress bg-light">
            <div class="progress-bar bg-success" role="progressbar" style="width: 80%" aria-valuenow="80" aria-valuemin="0"
                 aria-valuemax="100">80%</div>
          </div>
          <p>Firebase</p>
          <div class="progress bg-light">
            <div class="progress-bar bg-success" role="progressbar" style="width: 78%" aria-valuenow="78" aria-valuemin="0"
                 aria-valuemax="100">78%</div>
          </div>
          <p>Node Express</p>
          <div class="progress bg-light">
            <div class="progress-bar bg-success" role="progressbar" style="width: 65%" aria-valuenow="65" aria-valuemin="0"
                 aria-valuemax="100">65%</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Skills',
    data() {
      return {}
    },
    methods: {},
    mounted() {
      if (!$('#myCanvas').tagcanvas({
          textColour: '#ffffff',
          outlineColour: 'none',
          reverse: true,
          depth: 0.8,
          maxSpeed: 0.05
        }, 'tags')) {
        // something went wrong, hide the canvas container
        $('#myCanvasContainer').hide();
      }
      $(function () {
        $('[data-toggle="tooltip"]').tooltip()
      })
    }
  }
</script>

<style>
  .progress {
    margin-bottom: 1rem;
  }

  /* width */
  ::-webkit-scrollbar {
    width: 5px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: grey;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: blue;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: darkblue;
  }

  #skill-chart{
    margin-top: 60%;
  }

  canvas#myCanvas{
    height:400px;
    width:400px;
  }

  @media (max-width: 576px) {
    #skill-chart {
      margin-top: 75rem;
    }
    canvas#myCanvas{
      height:350px;
      width:100%;
    }
  }

</style>



